    font-size: 125px;
    height: 145px; }
  .power {
    font-size: 23px;
    letter-spacing: 13px; }
  .my-menu {
    ul {
      li {
        display: inline-block;
        a {
          padding: 35px 11px;
          color: #fff;
          text-decoration: none;
          box-sizing: border-box; } } } }
  .soc-icons {
    .fab {
      padding: 0 5px; } }
  .m-we_are {
    .w {
      left: 50%;
      margin-left: -147px; } }

  .blog-heading {
    .big-b {
      left: 50%;
      margin-left: -62px; } }

  .quick-action {
    .fas {
      font-size: 60px;
      color: #fff; }
    .quick-text {
      font-size: 14px; } }

  .key-feature {
    .key-iphoneSE {
      left: 50%;
      margin-left: 177px;
      img {
        width: 291px;
        height: 621px; } } }

  .team {
    .item-4 {
      margin-top: 70px; }
    .big-t {
      left: 50%;
      margin-left: -56px; }
    .team-block {
      left: 47px; } }

  .contact {
    &-inputs {
      input {
        width: 292px; } }
    &-message {
      textarea {
        width: 930px; } } } }





// ≥992px
@include media-breakpoint-down(md) {
  .h1 {
    font-size: 105px;
    height: 125px;
    letter-spacing: -9px; }
  .power {
    font-size: 21px;
    letter-spacing: 8px;
    text-align: center; }
