    text-align: center;
    ul {
      li {
        display: inline-block;
        a {
          padding: 35px 11px;
          color: #fff;
          text-decoration: none;
          box-sizing: border-box;
          &:hover {
            border-bottom: none;
            opacity: .75;
            transition: opacity .25s ease; } } } } }
  .soc-icons {
    .fab {
      padding: 0 0px; } }

  .m-we_are {
    .f-text {
      font-size: 14px; }
