      left: 50%;
      margin-left: -147px;
      margin-top: 0; } }

  .row-news {
    flex-direction: column;
    .black {
      width: 100%;
      height: 500px; }
    .white {
      background: #f3f3f3;
      width: 100%;
      height: 500px; }
    .img {
      background-repeat: no-repeat;
      background-size: cover;
      width: 100%;
      height: 500px; }
    .news-title, .date, .news-text {
      text-align: center; }
    .order {
      order: 2; } }

  .blog-heading {
    .big-b {
      left: 50%;
      margin-left: -62px; }
    .blog-text {
