  .quick-action {
    .fas {
      font-size: 45px;
      color: #fff; }
    .quick-text {
      font-size: 13px; } }

  .key-feature {
    .key-iphoneSE {
      left: 50%;
      margin-left: 87px;
      img {
        width: 261px;
        height: 591px; } }
    .key-text {
      font-size: 14px;
      margin-bottom: 40px; }
    .r-first {
      width: 350px;
      margin-bottom: 15px; }
    .r-second {
      width: 380px;
      margin-bottom: 15px; }
    .r-third {
      width: 398px;
      margin-bottom: 15px; } }

  .team {
    .item-3 {
      margin-top: 70px; }
    .big-t {
      left: 50%;
      margin-left: -56px; }
    .post-title {
      .title {
        font-size: 22px; } } }

  .contact {
    &-inputs {
      input {
        width: 211px; } }
    &-message {
      textarea {
        width: 690px; } } } }

